import React, { PureComponent, Suspense } from "react";
import "./scss/Header.scss";
import { isSocialEmbed, isTagbox, isTagShop } from "../../actions/api";
import { CloudHostUrl, TagboxUpload } from '../../constants'
import { isSocialWall } from "../../actions/api";
const HeaderType2 = React.lazy(() => import("./HeaderType/HeaderType2"));
const HeaderType3 = React.lazy(() => import("./HeaderType/HeaderType3"));
const HeaderType4 = React.lazy(() => import("./HeaderType/HeaderType4"));
const HeaderType5 = React.lazy(() => import("./HeaderType/HeaderType5"));
const HeaderTypeCustom = React.lazy(() => import('./HeaderType/HeaderTypeCustom'));
const SocialBanner = React.lazy(() => import('../ReviewHeader/SocialBanner'));


const HeaderRender = ({ Banner, LogoImage }) => {
  switch (Banner?.bannerType) {
    case 2:
      return <Suspense fallback={<></>}> <HeaderType2 Banner={Banner} LogoImage={LogoImage} /></Suspense>
    case 3:
      return <Suspense fallback={<></>}> <HeaderType3 Banner={Banner} LogoImage={LogoImage}/></Suspense>
    case 4:
      return <Suspense fallback={<></>}> <HeaderType4 Banner={Banner}/></Suspense>
    case 5:
      return <Suspense fallback={<></>}> <HeaderType5 Banner={Banner} /></Suspense>
    case 7:
      return <Suspense fallback={<></>}> <SocialBanner Banner={Banner} /></Suspense>

    default:
      return <></>
  }
}
export default class Header extends PureComponent {
  render() {
    const { Banner, BannerImage, LogoImage } = this.props;
    if (Banner?.CustomBannerImage && Banner?.CustomBannerImage.includes(CloudHostUrl)) Banner['CustomBannerImage'] = String(Banner?.CustomBannerImage).replace(TagboxUpload, "");
    if (Banner?.LogoImage && Banner?.LogoImage.includes(CloudHostUrl)) Banner['LogoImage'] = String(Banner?.LogoImage).replace(TagboxUpload, "");
    if (BannerImage.image && BannerImage.image.includes(CloudHostUrl)) BannerImage['image'] = String(BannerImage.image).replace(TagboxUpload, "");
	  let CustomBannerImage = { ...BannerImage };
	  
	  if (isTagShop) {

      const newName = BannerImage?.newName;
      CustomBannerImage.image = newName?.includes(CloudHostUrl) ? newName.replace(TagboxUpload, "") : newName;

    } else if (BannerImage?.image?.includes(CloudHostUrl)) {

      CustomBannerImage.image = BannerImage.image.replace(TagboxUpload, "");

    }

    return (
      <div className="tb_wall_header__">
        {Banner?.status === 1 ? <HeaderRender Banner={Banner} LogoImage={LogoImage} /> : ''} 
        {(isSocialWall || isSocialEmbed) && (BannerImage?.status === 1 && BannerImage?.newName !== '') ? 
        <Suspense fallback={<></>}> <HeaderTypeCustom ImageUrl={BannerImage.newName} /></Suspense> 
        : null}
        {isTagbox && (BannerImage?.status === 1 && BannerImage?.image !== '') ? 
        <Suspense fallback={<></>}> <HeaderTypeCustom ImageUrl={BannerImage.image} /></Suspense> 
        : ''}
        {isTagShop && (CustomBannerImage?.status === 1 && CustomBannerImage.image !== '' && CustomBannerImage.image.split(TagboxUpload)[1] !== '') ? 
        <Suspense fallback={<></>}> <HeaderTypeCustom ImageUrl={CustomBannerImage.image} /></Suspense> 
        : ''}
      </div>
    );
  }
}
